* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  display: inline-block;
}

h1, h2, h3, h4, p {
  margin: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  max-width: 1020px;
  margin: 0 auto;
}

.page {
  scroll-behavior: smooth;
  font-family: Poppins, sans-serif;
}

.page__body {
  color: #fff;
  background-color: #1d1d1d;
  height: 100vh;
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  width: 100%;
  height: 100vh;
  transition: transform .3s;
  position: fixed;
  top: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin: 0 20px 90px;
}

@media (min-width: 600px) {
  .page__section {
    margin: 0 72px 120px;
  }
}

@media (min-width: 1260px) {
  .page__section {
    margin: 0 120px 140px;
  }
}

.header {
  background-image: url("headerMidle.19960209.jpg");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  padding: 30px 0;
  font-family: inherit;
}

@media (min-width: 600px) {
  .header {
    background-position: center;
    padding-top: 35px;
  }
}

@media (min-width: 1260px) {
  .header {
    background-image: url("Cover_photo.cb255b8a.jpg");
  }
}

.header__content {
  height: 100%;
  margin: 0 20px;
}

@media (min-width: 600px) {
  .header__content {
    margin: 0 72px;
  }
}

@media (min-width: 1260px) {
  .header__content {
    margin: 0 120px;
  }
}

.header__container {
  height: 100%;
}

.header__grid {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 130px 1fr 24px 24px;
  height: 100%;
  display: grid;
}

@media (min-width: 600px) {
  .header__grid {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: 190px 1fr 32px 32px;
  }
}

@media (min-width: 1260px) {
  .header__grid {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: 190px 1fr 32px 32px;
  }
}

.header__contacts {
  background-image: url("Icon-Phone-call.c4e7dbb5.svg");
  background-repeat: no-repeat;
  background-size: cover;
  grid-area: 1 / 4;
  width: 24px;
  height: 24px;
  display: block;
  transform: translateX(-100%);
}

.header__contacts:before {
  content: "+1 234 555-55-55";
  color: #fff;
  letter-spacing: 2px;
  opacity: 0;
  pointer-events: none;
  width: max-content;
  padding-right: 20px;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: opacity .3s;
  display: block;
  position: relative;
  top: 50%;
  transform: translate(-100%, -50%);
}

.header__contacts:hover:before {
  opacity: 1;
  pointer-events: all;
}

@media (min-width: 600px) {
  .header__contacts {
    width: 32px;
    height: 32px;
  }
}

.header__nav {
  background-image: url("Icon-Burger-menu.ba728ffa.svg");
  background-repeat: no-repeat;
  background-size: cover;
  grid-area: 1 / 5;
  width: 24px;
  height: 24px;
  transition: background-image .3s;
  display: block;
}

.header__nav:hover {
  background-image: url("Icon-Burger-menu-hover.2cacadab.svg");
}

@media (min-width: 600px) {
  .header__nav {
    width: 32px;
    height: 32px;
  }
}

.header__title {
  letter-spacing: -1.5px;
  text-align: center;
  grid-area: 2 / 1 / -1 / 6;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: end;
  align-items: flex-end;
  margin-bottom: 80px;
  font-family: inherit;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  display: -ms-flexbox;
  display: flex;
}

@media (min-width: 600px) {
  .header__title {
    margin-bottom: 130px;
    font-size: 64px;
    line-height: 64px;
  }
}

@media (min-width: 1260px) {
  .header__title {
    margin-bottom: 200px;
    font-size: 80px;
    line-height: 80px;
  }
}

.logo {
  background-image: url("MyBiKE.b177947d.png");
  background-repeat: no-repeat;
  grid-area: 1 / 1;
}

@media (min-width: 600px) {
  .logo {
    background-image: url("MyBiKEbig.8189baf0.png");
  }
}

.menu {
  box-sizing: border-box;
  z-index: 2;
  background-color: #e5e5e5;
  height: 100vh;
  padding: 30px 20px;
  overflow-y: auto;
}

@media (min-width: 600px) {
  .menu {
    padding: 35px 72px 80px;
  }
}

@media (min-width: 1260px) {
