  }
}

.menu__content {
  max-width: 1020px;
  margin: 0 auto;
}

.menu__top {
  -ms-flex-pack: justify;
  justify-content: space-between;
  margin-bottom: 40px;
  display: -ms-flexbox;
  display: flex;
}

.menu__logo {
  background-image: url("MyBiKEmenu.e093accf.png");
  background-repeat: no-repeat;
  width: 130px;
  height: 38px;
  display: block;
}

@media (min-width: 1260px) {
  .menu__logo {
    background-image: url("MyBiKE.a51d6128.png");
    width: 187px;
    height: 56px;
  }
}

.menu__close {
  background-image: url("crros.01957458.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 24px;
  height: 24px;
  display: block;
}

@media (min-width: 600px) {
  .menu__close {
    width: 32px;
    height: 32px;
  }
}

.menu__list {
  margin-bottom: 40px;
}

.menu__list-item {
  letter-spacing: 2px;
  text-transform: uppercase;
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  width: max-content;
  margin-bottom: 30px;
  padding-bottom: 8px;
  font-family: inherit;
  font-size: 22px;
  font-weight: 600;
  line-height: 22px;
  transition: border .3s;
}

.menu__list-item:hover {
  border-color: #1d1d1d;
}

.menu__link {
  color: #1d1d1d;
}

.menu__contact {
  color: #1d1d1d;
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  margin-bottom: 15px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  line-height: 27px;
  transition: border .3s;
}

.menu__contact:hover {
  border-color: #1d1d1d;
}

.menu__book {
  letter-spacing: 2px;
  color: #1d1d1d;
  text-transform: uppercase;
  border-bottom: 1px solid #1d1d1d;
  width: max-content;
  padding-bottom: 8px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
  display: block;
}

