.about-section {
  padding-top: 60px;
}

.about-section__title {
  letter-spacing: -1.5px;
  padding-bottom: 30px;
  font-family: inherit;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
}

@media (min-width: 600px) {
  .about-section__title {
    font-size: 48px;
    line-height: 48px;
  }
}

@media (min-width: 1260px) {
  .about-section__title {
    font-size: 64px;
    line-height: 64px;
  }
}

.about-section__text {
  font-family: inherit;
  font-size: 23px;
  font-weight: 500;
  line-height: 32px;
}

.section__title {
  letter-spacing: -1.5px;
  text-align: center;
  color: #fff;
  margin-bottom: 48px;
  font-family: inherit;
  font-size: 32px;
  font-weight: 500;
  line-height: 32px;
}

@media (min-width: 600px) {
  .section__title {
    font-size: 48px;
    line-height: 48px;
  }
}

@media (min-width: 1260px) {
  .section__title {
    font-size: 64px;
    line-height: 64px;
  }
}

.section__cards {
  display: block;
}

@media (min-width: 1260px) {
  .section__cards {
    display: grid;
  }
}

.section__card {
  margin: 0 auto;
}

.section__card:not(:last-child) {
  margin-bottom: 40px;
}

@media (min-width: 600px) {
  .section__card:not(:last-child) {
    margin-bottom: 48px;
  }
}

@media (min-width: 1260px) {
  .section__card:not(:last-child) {
    margin-bottom: 0;
  }
}

.section__details {
  margin-bottom: 40px;
}

@media (min-width: 600px) {
  .section__details {
    margin-bottom: 48px;
  }
}

.section__form {
  margin-bottom: 48px;
}

.card {
  -ms-flex-direction: column;
  flex-direction: column;
  max-width: 390px;
  display: -ms-flexbox;
  display: flex;
}

.card__photo {
  cursor: pointer;
  -ms-flex-item-align: center;
  align-self: center;
  margin-bottom: 32px;
  transition: transform .3s;
}

.card__photo:hover {
  transform: scale(1.05);
}

@media (min-width: 600px) {
  .card__photo {
    width: 392px;
    height: 316px;
  }
}

@media (min-width: 1260px) {
  .card__photo {
    width: 324px;
    height: 264px;
  }
}

.card__title {
  margin-bottom: 8px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

.card__text {
  margin-bottom: 16px;
  font-family: inherit;
  font-size: 16px;
  line-height: 22px;
}

.card__price {
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

.details__photo {
  margin-bottom: 16px;
}

.details__photo-container {
  box-sizing: border-box;
  width: 100%;
  max-height: 320px;
  transition: transform .3s;
  position: relative;
  overflow: hidden;
}

@media (min-width: 600px) {
  .details__photo-container {
    height: 190px;
  }
}

@media (min-width: 1260px) {
  .details__photo-container {
    height: 320px;
    padding-bottom: 0;
  }
}

.details__photo-container:after {
  content: "";
  padding-bottom: 100%;
  display: block;
}

.details__photo-container:hover {
  transform: scale(1.05);
}

.details__image {
  cursor: pointer;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  position: absolute;
}

.details__subtitle {
  margin-bottom: 8px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

.details__text {
  font-family: inherit;
  font-size: 16px;
  line-height: 22px;
}

.button {
  text-align: center;
  cursor: pointer;
  color: #1d1d1d;
  background-color: #fff;
  border-radius: 40px;
  width: 100%;
  padding: 15px 48px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  transition: box-shadow .3s;
}

.button:hover {
  box-shadow: 1px 4px 16px rgba(232, 232, 240, .4);
}

.button:active {
  box-shadow: 1px 4px 16px rgba(232, 232, 240, .64);
}

.form__field {
  color: #292929;
  background: #f8f8fa;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 40px;
  width: 100%;
  height: 48px;
  margin-bottom: 16px;
  padding: 14px 16px;
  font-family: inherit;
  font-size: 14px;
  line-height: 20px;
  transition: border-color .3s;
  display: block;
}

.form__field:hover {
  border-color: #d5d7de;
}

.form__field:focus {
  border-color: #292929;
  outline: none;
