.button {
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  text-align: center;
  cursor: pointer;

  color: $taxt-black-color;
  background-color: $main-whit;
  width: 100%;
  padding: 15px 48px;
  border-radius: 40px;
  transition: box-shadow $animation-time;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
